import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import {url} from '../../../globalmodel/url';
import {ImageCrud} from '../moduleServices/imageCrud'
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'ad-image-crud',
  templateUrl: './ad-image-crud.component.html',
  styleUrls: ['./ad-image-crud.component.css']
})
export class AdImageCrudComponent implements OnInit {
@Input() categoryName;

@Input() activeImageObj=null;
@Output() imagecrudemit = new EventEmitter();
private form:FormData=new FormData();
constructor(private imagecrud:ImageCrud,private snackbar:MatSnackBar) { 

  }

  openSnackBar(message: string, action: string) {
    this.snackbar.open(message, action, {
      duration: 2000,
    });
  }
  ngOnInit(){

}
imageEmit(obj){
  this.imagecrudemit.emit(obj);
}

  uploadImage(inputFile:HTMLInputElement){

    this.form.set('category',this.categoryName);
this.form.set('label',this.activeImageObj.label);
      if(inputFile.files.length!=0){
    this.form.set('adImage',inputFile.files[0]);
    this.form.set('crud','add');
    this.imagecrud.uploadFile(url.localAdImageAdd,this.form).subscribe(imageobj=>{
      if(imageobj!=null && imageobj.imageUrl!=null){
                console.log(imageobj)
        this.activeImageObj.label=imageobj.label;
    this.activeImageObj.imageobj.url=imageobj.imageUrl;
    console.log(this.activeImageObj)
        this.openSnackBar('Image Uploaded Successfully','');
        this.imageEmit({'crud':'add','url':imageobj.imageUrl,'label':imageobj.label,'key':imageobj.imagekey});
      }

    },err=>{
      this.openSnackBar(err,'')
    })
  }
}
}
